.wp-block-wc-booster-usp {
    padding: 15px;
}
ul.wc-booster-usps {
    padding: 0 0 0 10px;
    margin: 0;
    list-style: none;

    li {
        display: flex;
        line-height: 1;
        margin-bottom: 12px;
        align-items: center;
        i {
           
            margin-right: 12px;
        }
    }
}

.wp-block-wc-booster-usp p {
    margin: 0 0 15px;
    font-weight: bold;
    font-size: 21px;
    padding-left: 10px;
}